@import url(https://fonts.googleapis.com/css2?family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding: 0; margin: 0;box-sizing: border-box; -webkit-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none;}

body{
    font-family: 'Nunito', sans-serif;
    letter-spacing: 1px;
    font-weight: 400;
    color: rgb(82, 82, 82);
    min-height: 100wh;
}
input, button, select{
    border: none;
    outline: none!important;
}
input, select{
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);
    padding: 10px;
    background-color: inherit;
    color: inherit;
}
img{
    width: 100%;
    height: auto;
}
a{
    font-weight: 800;
    color: #1976d2;
}
a:hover{
    text-decoration: none;
}

@media(max-width: 768px){
    body{
        font-size: 14px;
    }
    a{
        font-size: 14px!important;
    }
    table, h6, button{
        font-size: 12px!important;
    }
}
.news-item{
    padding: 20px;
    box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
    background-color: #f8f9fa;
    border-radius: 10px;
    margin-bottom: 40px;
}
.news-title{
    font-size: 18px;
}
.read-more{
    font-size: 12px;
    transition: 0.3s;
    display: inline-block;
}
.read-more:hover{
    transform: translateX(10px);
}
.about{
    width: 100wh;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
}

iframe{
    max-width: 90%;
    max-height: 300px;
}
.course-regis{
    min-height: 72vh;
}

.regis-control{
    display: flex;
    justify-content: center;
    align-items: center;
}

.regis-table{
    font-size: 14px;
    width: 100%;
}
.regis-note span{
    margin: 0 10px;
}

@media (max-width: 768px){
    .regis-control{
        flex-direction: column;
    }
}
.timetable-show td{
    min-width: 90px;
    max-width: 90px;
}

.timetable-show tbody tr td h6{
    font-weight: 800;
    color: #1976d2;
}

.bottom-button .btn{
    margin: 20px;
}

table{
    font-size: 14px;
}

@media(max-width: 768px){
    .timetable-control{
        flex-direction: column;
    }
    button{
        font-size: 14px!important;
    }
}
.edit-form{
    max-width: 500px;
    margin: 80px auto;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 12px 15px rgba(0, 0, 0, 0.2);
    background-color: #f8f9fa;
    flex-direction: column;
}
.edit-form td{
    padding: 10px;
}
.edit-form .label{
    font-weight: 800;
    color: #1976d2;
}
.edit-form-header{
    font-size: 18px;
    font-weight: 800;
    text-transform: uppercase;
    letter-spacing: 5px;
    color: rgb(214, 86, 47);
}
.edit-form input{
    background-color: inherit;
    color: inherit;
}
.button-group{
    width: 100%;
    display: flex;
    justify-content: space-around;
}
.not-found{
    background-image: linear-gradient(to bottom, #02142e, #001235, #00103b, #000d40, #020844);
    max-width: 100vw;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    overflow: hidden;
    position: relative;
}

img.rocket{
    width: 150px;
    height: auto;
    transform: rotate(90deg);
    position: absolute;
    top: 60%;
    left: -150px;
    -webkit-animation: rocket 6s linear infinite;
            animation: rocket 6s linear infinite;
}


@-webkit-keyframes rocket{
    0%{left: -150px;}
    100%{left: 120%;}
}


@keyframes rocket{
    0%{left: -150px;}
    100%{left: 120%;}
}

.not-found .star{
    width: 3px;
    height: 3px;
    background: #fff;
    position: absolute;
    right: 0;
    z-index: -1;
    box-shadow: 0 0 15px rgba(255, 255, 255, 0.4);
    -webkit-animation: twinkle 2s linear infinite;
            animation: twinkle 2s linear infinite;
}
@-webkit-keyframes twinkle{
    0%{box-shadow: 0 0 0px rgba(255, 255, 255, 0.4);}
    20%{box-shadow: 0 0 5px rgba(255, 255, 255, 0.4);}
    40%{box-shadow: 0 0 15px rgba(255, 255, 255, 0.4);}
    60%{box-shadow: 0 0 0px rgba(255, 255, 255, 0.4);}
    80%{box-shadow: 0 0 5px rgba(255, 255, 255, 0.4);}
    100%{box-shadow: 0 0 15px rgba(255, 255, 255, 0.4);}
}
@keyframes twinkle{
    0%{box-shadow: 0 0 0px rgba(255, 255, 255, 0.4);}
    20%{box-shadow: 0 0 5px rgba(255, 255, 255, 0.4);}
    40%{box-shadow: 0 0 15px rgba(255, 255, 255, 0.4);}
    60%{box-shadow: 0 0 0px rgba(255, 255, 255, 0.4);}
    80%{box-shadow: 0 0 5px rgba(255, 255, 255, 0.4);}
    100%{box-shadow: 0 0 15px rgba(255, 255, 255, 0.4);}
}

.not-found .title{
    text-align: center;
    color: #fff;
    margin-top: 50px;
    letter-spacing: 3px;
}

.not-found h3{
    font-size: 100px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.not-found .title p{
    font-size: 20px;
    margin-top: 20px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.not-found button a{
    border: none;
    outline: none;
    padding: 10px 15px;
    font-size: 15px;
    margin-top: 30px;
    transition: 0.3s;
    cursor: pointer;
    border-radius: 5px;
    color: white;
    background-color: rgb(151, 14, 14);
    font-weight: 600;
}

.not-found button:hover{
    background-color: rgb(116, 13, 13);
}

.not-found button:active{
    transform: scale(1.2);
}

*{padding: 0; margin: 0; box-sizing: border-box;}

.login-page{
    width: 100wh;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(233, 201, 201);
    z-index: 999;
}

.login-form{
    max-width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.4);
    border-radius: 10px;
    background-color: #fff;
}

.form-group, .login-button{
    margin-top: 20px;
}

.logo{
    max-width: 150px;
}
